#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.bg{
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh ;
}
.introContent{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font-size: 1.75rem;
    font-weight: 100;
}
.introName{
    color: #01a4cc;
}
.introP{
    font-size: medium;
    font-weight: 300;
}
.btn{
    background: white;
    margin: 1rem 0;
    padding:  0.75rem 1.5rem;
    border-radius: 2rem;
    border: none;
}

.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

@media screen and (max-width: 840px) {
    .bg{
        right: -5vw;
    }
    .introContent{
        font-size: 10vw;
    } 
}
@media screen and (max-width: 480px) {
    .bg{
        right: -5vw;
    }
}